.App {
  text-align: center;
}

.App-header {
  background-color: #222;
  height: 40px;
  padding: 20px;
background: -webkit-linear-gradient(left, #0A0000 0%, #180000 50%, #0D0202 100%);
background: -webkit-gradient(linear, left top, right top, from(#0A0000), color-stop(50%, #180000), to(#0D0202));
background: -o-linear-gradient(left, #0A0000 0%, #180000 50%, #0D0202 100%);
background: linear-gradient(to right, #0A0000 0%, #180000 50%, #0D0202 100%);
}

.App-title {
  font-size: 20px;
}

.App-intro {
  font-size: large;
}




