body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: #0a0000;
  color: #b8b8b8;
  background: -webkit-linear-gradient(
    left,
    #0a0000 0%,
    #400000 50%,
    #0d0202 100%
  );
  background: -webkit-gradient(linear, left top, right top, from(#0a0000), color-stop(50%, #400000), to(#0d0202));
  background: -webkit-linear-gradient(left, #0a0000 0%, #400000 50%, #0d0202 100%);
  background: -o-linear-gradient(left, #0a0000 0%, #400000 50%, #0d0202 100%);
  background: linear-gradient(to right, #0a0000 0%, #400000 50%, #0d0202 100%);
  text-align: center;
}

.server{
  width: 25%;
  text-align: center;
}
div.blueTable {
  border: 1px solid #a40000;
  background-color: #0a0000;
  width: 50%;
  text-align: center;
  border-collapse: collapse;
  -ms-flex-item-align: center;
      align-self: center;
}

div.serverInfo {
  border: 1px solid #a40000;
  background-color: #0a0000;
  width: 100%;
  text-align: center;
  border-collapse: collapse;
  -ms-flex-item-align: center;
      align-self: center;
} 

.divTable.blueTable .divTableCell,
.divTable.blueTable .divTableHead {
  border: 1px solid #5c0000;
  padding: 3px 2px;
  width: 40%;
  -ms-flex-item-align: center;
      align-self: center;
}
.divTable.blueTable .divTableBody .divTableCell {
  font-size: 13px;
  color: #b8b8b8;
  -ms-flex-item-align: center;
      align-self: center;
}
.divTable.blueTable .divTableRow:nth-child(even) {
  background: #290000;
  text-align: center;
}
.divTable.blueTable .divTableHeading {
  background: #b50000;
  background: -webkit-linear-gradient(
    top,
    #c74040 0%,
    #bc1919 66%,
    #b50000 100%
  );
  background: -webkit-gradient(linear, left top, left bottom, from(#c74040), color-stop(66%, #bc1919), to(#b50000));
  background: -webkit-linear-gradient(top, #c74040 0%, #bc1919 66%, #b50000 100%);
  background: -o-linear-gradient(top, #c74040 0%, #bc1919 66%, #b50000 100%);
  background: linear-gradient(to bottom, #c74040 0%, #bc1919 66%, #b50000 100%);
  border-bottom: 2px solid #3d0000;
}
.divTable.blueTable .divTableHeading .divTableHead {
  font-size: 15px;
  font-weight: bold;
  color: #000000;
  border-left: 2px solid #300000;
}
.divTable.blueTable .divTableHeading .divTableHead:first-child {
  border-left: none;
}

.blueTable .tableFootStyle {
  font-size: 14px;
}
.blueTable .tableFootStyle .links {
  text-align: right;
}
.blueTable .tableFootStyle .links a {
  display: inline-block;
  background: #1c6ea4;
  color: #ffffff;
  padding: 2px 8px;
  border-radius: 5px;
}
.blueTable.outerTableFooter {
  border-top: none;
}
.blueTable.outerTableFooter .tableFootStyle {
  padding: 3px 5px;
}
/* DivTable.com */
.divTable {
  display: table;
  text-align: center;
}
.divTableRow {
  display: table-row;
}
.divTableHeading {
  display: table-header-group;
}
.divTableCell,
.divTableHead {
  display: table-cell;
}
.divTableHeading {
  display: table-header-group;
}
.divTableFoot {
  display: table-footer-group;
}
.divTableBody {
  display: table-row-group;
  text-align: center;
}



